*, :before, :after {
  box-sizing: border-box;
}

ul[role="list"] {
  margin: 0;
  padding: 0;
  list-style: none;
}

img {
  max-width: 100%;
  display: block;
}

@font-face {
  font-family: Inter;
  src: url("Inter_18pt-Regular.a443b269.ttf") format("ttf");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: Inter;
  src: url("Inter_28pt-Bold.2c636f6f.ttf") format("ttf");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}

body {
  background-color: #fff;
  font-family: Inter, sans-serif;
  font-size: 16px;
}

:root {
  --font-size-base: # ($ font-size-base);
  --font-size-headings: # ($ font-size-headings);
  --spacing: # ($ spacing);
  --max-width: # ($ max-width);
  --height-img: # ($ height-img);
  --height-nav: # ($ height-nav);
  --columns: # ($ columns);
  --gutter: # ($ gutter);
  --offset: # ($ offset);
  --width-offset: # ($ width-offset);
  --vertical-space-small: # ($ vertical-space-small);
  --vertical-space-large: # ($ vertical-space-large);
  --vertical-space: # ($ vertical-space);
  --border-nav: # ($ border-nav);
  --border-radius-regular: # ($ border-radius-regular);
  --color-background-primary: # ($ color-background-primary);
  --color-background-secondary: # ($ color-background-secondary);
  --color-accent: # ($ color-accent);
  --color-protection: # ($ color-protection);
  --font-base-size: # ($ font-base-size);
  --font-family-base: # ($ font-family-base);
  --font-family-heading: # ($ font-family-heading);
}

@media (width >= 700px) {
  :root {
    --vertical-space: var(--vertical-space-large);
  }
}

@media (prefers-color-scheme: dark) {
  :root {
    --color-background-primary: #000;
    --color-background-secondary: #fff;
    --color-accent: #600;
    --color-protection: #ffffff80;
  }
}

:fullscreen {
  background: #fff;
}
